body {
  background-color: #F2F2F2;
}

button {
  background-color: transparent;
  border: none;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 32px;
}

.container {
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

h1 {
  letter-spacing: 10px;
  font-size: 20px;
  color: #424242;
  margin-bottom: 32px;
  text-align: center;
}

.form {
   position: relative;
   overflow: hidden;
   width: 100%;
   -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
           box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
}

textarea {
    width: 100%;
    min-height: 170px;
    border: 0px;
    letter-spacing: 1px;
    padding: 24px;
    font-size: 16px;
    color: #484a4c;
    display: block;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    resize: none;
}

textarea::-webkit-input-placeholder {
    opacity: .3;
}

textarea::-moz-placeholder {
    opacity: .3;
}

textarea:-ms-input-placeholder {
    opacity: .3;
}

textarea::placeholder {
    opacity: .3;
}

.button {
    position: absolute;
    bottom: 24px;
    right: 24px;
    border: none;
    font-size: 16px;
    letter-spacing: 1px;
    color: white;
    background-color: #EAEAEA;
    padding: 10px 36px;
}

.button--active {
  background-color: #4990E2;
}

.middle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin: 40px auto;
  font-size: 16px;
  color: #c6c6c6;
  letter-spacing: 2px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  line-height: 10px;
  position: relative;
}

.middle:before,
.middle:after {
  content: '';
  height: 1px;
  top: 5px;
  background-color: #c6c6c6;
  opacity: .5;
  position: absolute;
  width: 42%;
}

.middle:before {
  left: 0;
}

.middle:after {
  right: 0;
}



